import React, { Component } from 'react';
import Keyboard from "react-simple-keyboard";
import MetaTags from 'react-meta-tags';
import 'react-simple-keyboard/build/css/index.css';
import './index.css'

class InputMobileStandard extends Component {
    constructor(props) {
        super(props);

        this.state = {
            problems: "",
            recording: false,
            current_signals: ""
        };

        this.movement_signals = []
        this.big_motion_times = []

        if( window.DeviceMotionEvent)
        {
            window.addEventListener("devicemotion", this.motion, false);
        }
        else
        {
            this.setState({problems: "DeviceMotionEvent is not supported"});
        }
    }


    motion = (event) => {
        if (this.state.recording)
        {
            let timestamp = Math.round(event.timeStamp);
            let motion_data = [timestamp, Math.round(100 * event.rotationRate.alpha), Math.round(100 * event.rotationRate.beta), Math.round(100 * event.rotationRate.gamma), 
                Math.round(100 * event.accelerationIncludingGravity.x), Math.round(100 * event.accelerationIncludingGravity.y), Math.round(100 * event.accelerationIncludingGravity.z)]
            
            /*
            this.setState({current_signals: timestamp.toString() + "\n" +
                Math.round(10 * event.rotationRate.alpha).toString() + "\n" +
                Math.round(10 * event.rotationRate.beta).toString() + "\n" +
                Math.round(10 * event.rotationRate.gamma).toString() + "\n" +
                Math.round(10 * event.accelerationIncludingGravity.x).toString() + "\n" + 
                Math.round(10 * event.accelerationIncludingGravity.y).toString() + "\n" + 
                Math.round(10 * event.accelerationIncludingGravity.z).toString() + "\n"
            })
            */
            this.movement_signals.push(motion_data)
        }
    }

    start = () => {
        if (this.state.recording)
        {
            this.props.sendDataToAPI(this.movement_signals, this.big_motion_times)
            this.movement_signals = []
            this.big_motion_times = []
        }

        this.setState({recording: !this.state.recording})
    }
    

    restart = () => {
        if (this.state.recording)
        {
            this.movement_signals = []
            this.big_motion_times = []
        }
    }


    registerBigMotion = (event) => {
        this.big_motion_times.push(event.timeStamp)
    }


    render() {
        return (
            <React.Fragment>
                <MetaTags>
                    <meta name="viewport" content="width=device-width; initial-scale=0.5, maximum-scale=0.5, minimum-scale=1.0; " />
                    <meta name="viewport" content="width=device-width, user-scalable=no"></meta>
                    <meta http-equiv='cache-control' content='no-cache'></meta>
                    <meta http-equiv='expires' content='0'></meta>
                    <meta http-equiv='pragma' content='no-cache'></meta>
                </MetaTags>

                <p>{this.state.problems ? this.state.problems : "Motion is ready to be recorded"}</p>
                <p>uploaded: {this.state.uploaded}</p>
                <p><b>{this .state.recording ? "RECORDING" : ""}</b></p>
                {/*this.state.current_signals.split("\n").map((i,key) => {
                    return <p key={key}>{i}</p>;}) */}
                
                <div className="footer" onTouchStart={this.handleTouchStart} onTouchEnd={this.handleTouchEnd}>
                    <button type="button" className="btn btn-success btn-block" onClick={this.start}>{this.state.recording ? "STOP" : "START"}</button>
                    <button type="button" className="btn btn-warning btn-block" onClick={this.restart}>RESTART</button>
                    <button type="button" className="btn btn-primary btn-block" onClick={this.registerBigMotion}>BIG MOTION</button>
                            
                    <Keyboard
                        keyboardRef={r => (this.keyboard = r)}
                        mergeDisplay="true"
                        layout={{
                            default: [
                                "1 2 3 4 5 6 7 8 9 0",
                                "q w e r t y u i o p",
                                "a s d f g h j k l",
                                "{shift} z x c v b n m {backspace}",
                                ", {space} ."
                            ],
                            shift: [
                                "1 2 3 4 5 6 7 8 9 0",
                                "Q W E R T Y U I O P",
                                "A S D F G H J K L",
                                "{shift} Z X C V B N M {backspace}",
                                ", {space} ."
                            ],
                        }}
                        display={{
                            "{escape}": "esc ⎋",
                            "{tab}": "tab ⇥",
                            "{backspace}": "⌫",
                            "{capslock}": "caps lock ⇪",
                            "{shift}": "⇧",
                            "{controlleft}": "ctrl ⌃",
                            "{controlright}": "ctrl ⌃",
                            "{altleft}": "alt ⌥",
                            "{altright}": "alt ⌥",
                            "{metaleft}": "cmd ⌘",
                            "{metaright}": "cmd ⌘",
                            "{abc}": "ABC",
                            "{space}": "space"
                        }}
                        disableButtonHold={true}
                        buttonTheme = {[
                            {
                              class: "margin-left",
                              buttons: "A a ,"
                            },
                            {
                              class: "margin-right",
                              buttons: "L l ."
                            },
                            {
                                class: "space",
                                buttons: "{space}"
                            }
                          ]}
                        >
                    </Keyboard>
                </div>
            </React.Fragment>
        );
    }
}

export default InputMobileStandard;