import React, { Component } from 'react';
import InputMobileStandard from './InputMobileStandard'

const axios = require('axios');

export class Typing extends Component {
  static displayName = Typing.name;
    constructor(props) {
        super(props);


        this.state = {
            taskCompleted: false,
            uploaded: false,
        }
    }

    sendDataToAPI = (movement_signals, big_motion_times) => {
        let endpoint = "https://keystrokecollector.azurewebsites.net/api/savejson?code=5PEryaE4Pr/GDZ2VG08wMrC1zaMVYCdaFzzUXiyLjo5I3ZkDx58Xng=="
        
        let obj_to_send = {
            "movement_signals": movement_signals, 
            "big_motion_times": big_motion_times
        }

        this.setState({uploaded: false})

        axios.post(endpoint, obj_to_send)
        .then((response) =>
        {
            this.setState({uploaded: true})
        })
        .catch((error) =>
        {
            this.setState({uploaded: true})
            console.log("An error occured while transfering data:", error)
        });
    }


    render() {
        return( 
            <InputMobileStandard sendDataToAPI={this.sendDataToAPI} uploaded={this.state.uploaded}></InputMobileStandard>
        )}
} 